$background1: #414141;
$background2: #262626;
$background3: #24364d;
$background4: #444;
$background5: #5b2900;
$border1: #000;
$font-color: #fff;
$font-color2: #b8b8b8;
$close1: #fff;
$close2: #ff0000;
$invisible: rgba(#000,0);

#settings_install {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow: hidden;

  ul.settings_install.menu {
    border-right: 1px solid $border1;
    display: inline-block;
    margin: 0px;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 0px;
    user-select: none;

    li {
      list-style: none;
      margin: 0px;
      padding: 20px;
      transition: 100ms;
      cursor: pointer;
      position: relative;
      height: 40px;
      padding: 2px;
      border-bottom: 1px solid $border1;

      &.active {
        background-color: $background4; }

      &:hover {
        background-color: $background4; }

      div.infos {
        display: inline-block;
        width: calc(100% - 40px);
        height: 100%;
        padding: 2px;

        p.name {
          margin: 0px;
          color: $font-color; }

        ul.subinfos {
          display: inline-block;
          margin: 0px;
          padding: 0px;

          li {
            color: $font-color2;
            display: inline;
            border: none;
            margin: 0px;
            padding: 0px 5px 0px 5px;
            background-color: rgba(#000,0);
            font-size: 12px;

            & + li {
              border-left: 2px solid $font-color2; } } } }

      button.installbutton {
        width: 30px;
        height: 30px;
        position: relative;
        top: -7px;
        border: none;
        border-radius: 4px;
        color: #fff;
        background-color: $invisible;
        outline: none;
        cursor: pointer;

        &:hover {
          background-color: $background3; }

        &:active {
          outline: none;
          background-color: $background5; }

        &.clicked {
          &:hover {
            background-color: $invisible; }

          &:active {
            outline: none;
            background-color: $invisible; }

          i.mdi.mdi-download {
            &::before {
              animation-name: installspin;
              animation-duration: 4000ms;
              animation-iteration-count: infinite;
              animation-timing-function: linear;
              content: "\F493"; } } } } } } }

@keyframes installspin {
  from {
    transform: rotate(0deg); }

  to {
    transform: rotate(360deg); } }
